.header {
    display: flex;
    justify-content: space-between;
    height: 7vh;
    border-bottom: 1px solid black;
}

.title {
    font-family: "Graduate";
    font-size: 2.5rem;
}

.menu {
    width: 7vh;
}

.search {
    width: 7vh;
}

.header-menu {
    position: relative;
    height: 100%;
    width: 100%;
    z-index: 3;
}

.menu-content {
    display: flex;
    flex-direction: column;
    margin-top: -15%;
    width: 0;
}

.menu-item {
    line-height: 5vh;
    height: 5vh;
    border: 1px solid black;
    background-color: #ffffff;
    z-index: 2;
    text-align: center;
    cursor: pointer;
    color: black;
    text-decoration: none;
}

.menu-content a {
    color: black;
    text-decoration: none;
}

.hide .menu-item {
    display: none;
    width: 0;
}

.menu-content.show {
    width: 60vw;
    transition: width linear 200ms;
}

.menu-icon {
    height: 100%;
    width: 100%;
    padding-top: 15%;
}

.menu-icon .bar {
    width: 40%;
    height: 15%;
    border-bottom: 2px solid black;
    margin: auto;
}

.search-icon {
    width: 100%;
    height: 100%;
}

.search-icon .glass {
    width: 35%;
    height: 35%;
    border-radius: 100%;
    border: 2px solid black;
    margin: auto;
    margin-top: 25%;
    position: relative;
}

.search-icon .handle {
    position: relative;
    width: 20%;
    border-bottom: 2px solid black;
    transform: rotate(45deg);
    margin-left: 55%;
}
