.card {
    border: 1px solid black;
    padding: 1vw;
    width: 36vw;
    height: 48vw;
    max-width: 132px;
    max-height: 176px;
    min-width: 120px;
    min-height: 160px;
    margin: 1vh 1vw;
    transition: width 0.1s, height 0.1s;
    cursor: pointer;
}

.card .bar {
    height: 5px;
    width: 100%;
}

.card.full-screen {
    position: fixed;
    z-index: 2;
    top: 13vh;
    margin: 0 auto;
    width: 95vw;
    height: 65vh;
    max-width: none;
    max-height: none;
    transition: width 0.1s, height 0.1s;
    background-color: #ffffff;
}

.card .card-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
